const orderDetail = r => require.ensure([], () => r(require('../mdc/order/orderDetail.vue')), 'orderDetail');
const mineOrder = r => require.ensure([], () => r(require('../mdc/order/mineOrder.vue')), 'mineOrder');

// 例子首页
export default [{
  name: 'orderDetail',
  path: 'orderDetail',
  component: orderDetail,
  meta: {
    // hasFooter: true,
    // noScroll: true
  }
}, {
  // 暂时放在订单模块，需要放在我的模块
  name: 'mineOrder',
  path: 'mineOrder',
  component: mineOrder,
  meta: {
    // hasFooter: true,
    // noScroll: true
  }
}]



