<template>
   <svg class="icon" :style="{ color: iconColor }" aria-hidden="true">
      <use :xlink:href="iconName"></use>
   </svg>
</template>
<script>

export default {
    props:{
        value: String,
        color: String
    },
    computed: {
      iconName() {
        return `#icon-${this.value}`
      },
      iconColor() {
        return `#${this.color}`
      }
    }
};
</script>



