import Vue from 'vue'
import Router from 'vue-router'
import store from '../vuex/store'

import main from './main'
import order from './order'
// import home from './home'
import mine from './mine'
import login from './login'
import home from './home'

Vue.use(Router)
const router = new Router({
	routes: [{
		name: "base",
		path: '/',
		redirect: 'mdc'
	}, {
		name: "baseHome",
		path: '/baseHome',
		component(resolve) {
			require.ensure([], () => resolve(require('../pages/home/BaseHome.vue')), 'baseHome')
		},
		meta: {
			hasFooter: true,
			noScroll: false
		},
		children: [...main, ...mine, ...login, ...[{
			name: "demo",
			path: 'demo',
			component(resolve) {
				require.ensure([], () => resolve(require('../pages/demo/Demo.vue')), 'Demo')
			},
			meta: {
				hasFooter: true,
				noScroll: true
			}
		}]]
	}, {
		name: "mdc",
		path: '/mdc',
		component(resolve) {
			require.ensure([], () => resolve(require('../mdc/home/home.vue')), 'mdc')
		},
		meta: {
			hasFooter: true,
      noScroll: false,
      isHome: true
		},
	}, {
		name: "mdcOrder",
		path: '/mdcOrder',
		component(resolve) {
			require.ensure([], () => resolve(require('../mdc/home/homeOrder.vue')), 'homeOrder')
		},
		meta: {
			hasFooter: true,
      noScroll: false,
      isHome: true
		},
		children: [...main, ...login,...home,...order]
	}, {
		name: "mdcMine",
		path: '/mdcMine',
		component(resolve) {
			require.ensure([], () => resolve(require('../mdc/home/homeMine.vue')), 'homeMine')
		},
		meta: {
			hasFooter: true,
      noScroll: false,
      isHome: true
    },
    children: [...mine]
	}, {
    name: "location",
		path: '/location',
		component(resolve) {
			require.ensure([], () => resolve(require('../mdc/home/location.vue')), 'location')
		},
  }]
})


function directRightUrl () {
	console.log(window.location.href)
	// if (window.location.href !== url) {
  //   window.location.href = url
  // }
}
//制作权限控制
router.beforeEach((to, from, next) => {
	// directRightUrl()
  console.log(to)
	//是否显示底部导航
	store.commit('SHOW_FOOTER', to.meta.hasFooter);
	//判断是否为baseHome页面
	store.commit('IS_HOME', to.meta.isHome);
	//页面是否需要滚动
  store.commit('NO_SCROLL', to ? to.meta.noScroll : true);
  // 首页底部标签选中状态
  store.commit('HOME_SELECTED', to.name);
	next()
})
Router.prototype.goBack = function (path) {
	this.isBack = true
	if (typeof path == 'string') {
		router.push(path)
	} else {
		window.history.go(-1)
	}
}

export default router



