<template>
  <div v-bind:class="isMobile?'platfrom-ios':''">
    <!-- v-bind:style="{'overflow-y':isScroll && !$store.state.common.noScroll? 'hidden':'hidden'}" -->
    <div v-bind:class="[$store.state.common.hasFooter?'has-footer':'']" class="child-view scroll-content" id="scroll-content">
      <transition :name="transitionName">
        <router-view></router-view>
      </transition>
    </div>
		<!-- 底部导航 -->
    <transition name="slideInUp">
      <mt-tabbar v-show="$store.state.common.hasFooter" v-model="selected">
        <mt-tab-item id="1">
          <svg class="icon animated" aria-hidden="true" slot="icon">
            <use xlink:href="#icon-lujing1"></use>
          </svg>
          首页
        </mt-tab-item>
        <mt-tab-item id="2">
          <svg class="icon animated" aria-hidden="true" slot="icon">
            <use xlink:href="#icon-lujing2"></use>
          </svg>
          订单
        </mt-tab-item>
        <mt-tab-item id="3">
          <svg class="icon animated" aria-hidden="true" slot="icon">
            <use xlink:href="#icon-lujing3"></use>
          </svg>
          我的
        </mt-tab-item>
      </mt-tabbar>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      selected: this.$store.state.common.homeSelected,
      isScroll: true,
      transitionName: "fade",
      showHome: false,
      positons: {}
    }
  },
  watch: {
		//监听控件弹出
    ['$store.state.common.popObj']: {
      handler(newVal) {
        this.isScroll = true;
        for (var key in newVal) {
          if (newVal[key]) {
            this.isScroll = false;
          }
        }
        this.$store.commit('SHOW_FOOTER', this.isScroll)
      },
      deep: true
		},
		/**
		 * 点击tab选中
		 */
    selected(newVal, oldVal) {
      this.$store.commit('TAB_SELECTED', newVal)
      if(newVal == 1) {
        this.$router.replace({ name: "mdc" })
      }else if (newVal == 2) {
        this.$router.replace({ name: "mdcOrder" })
      }else if (newVal == 3) {
        this.$router.replace({ name: "mdcMine" })
      }
      // this.transitionName = oldVal < newVal ? "fade" : "slideInLeft";
    }
  },
  methods: {
    //滚动到相对位置
    toPositon(key) {
      document.getElementById("scroll-content").scrollTo(0, this.positons[key] ? this.positons[key].y : 0)
    },
    //保存位置信息
    savePositon(key) {
      this.positons[key] = {
        y: document.getElementById("scroll-content") && document.getElementById("scroll-content").scrollTop
      }
    }
  },
  created() {
    // utils.wx.wxConfig().then(req => {
    //   console.log("获取微信配置返回数据", req)
    // });
  }

}
</script>
<style lang="scss">
.scroll-content {
  z-index: 0; // -webkit-overflow-scrolling: touch;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; // z-index: 1;
  display: block;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
  // contain: size style layout;
}
</style>




