<template>
   <mt-popup v-model="visible" class="hc-address"
             position="bottom">
      <mt-picker :slots="slots" @change="onValuesChange"  value-key='nodeName' show-toolbar>
        <span class="mint-datetime-action mint-datetime-cancel" @click="visible = false;$emit('cancel')">{{ cancelText }}</span>
        <span class="mint-datetime-action mint-datetime-confirm" @click="confirm">{{ confirmText }}</span>
      </mt-picker>
   </mt-popup>
</template>
<script>
import {queryAll} from '../../../api/axios.js';
export default {
  props:{
    cancelText: {
      type: String,
      default: '取消'
    },
    confirmText: {
      type: String,
      default: '确定'
    },
  },
   data(){
     return{
        visible: false,
        address:[],
        slots:[],
        selectedValue:[],
     }
   },
   cteated(){
   },
   mounted(){
      this.getAll();
   },
   methods:{
      open() {
        this.visible = true;
      },
      close() {
        this.visible = false;
      },
      // 点击确认
      confirm() {
        this.visible = false;
        this.$emit('confirm', this.selectedValue);
      },
      onValuesChange(picker, values) {
        this.selectedValue = values;
        if(!values[0]){
              this.$nextTick(()=>{
                  if(this.myAdress){
                        // 赋默认值
                  }else{
                    picker.setValues([this.address[0],this.address[0].children[0],this.address[0].children[0].children[0]])
                  }
              });
          }else{
              picker.setSlotValues(1, values[0].children);
              let town = [];
              if(values[1]){
                town = values[1].children;
              }
              picker.setSlotValues(2,town);
          }
      },
      getAll(){
        let address = JSON.parse(localStorage.getItem('HC_ADDRESSES'));
        if(address){
          this.address = address;
        }else{
          queryAll().then((data)=>{
            this.address = data.result;
            localStorage.setItem('HC_ADDRESSES',JSON.stringify(data.result));
          })
        }
        // 默认值
        this.selectedValue = [this.address[0],this.address[0].children[0],this.address[0].children[0].children[0]]
        this.slots = [
            {
              flex: 1,
              values:  this.address,
              className: 'slot1',
              textAlign: 'center'
            }, {
              divider: true,
              content: '-',
              className: 'slot2'
            }, {
              flex: 1,
              values:  this.address[0].children,
              className: 'slot3',
              textAlign: 'center'
            }, {
              divider: true,
              content: '-',
              className: 'slot4'
            }, {
              flex: 1,
              values:  this.address[0].children[0].children,
              className: 'slot5',
              textAlign: 'center'
            }
        ]
      }
   }
}
</script>
<style lang="scss" scoped>
    .hc-address{
      &.mint-popup {
        height: auto;
      }
    }
</style>




