import axios from './config'
import qs from 'qs'
import { Indicator, MessageBox } from 'mint-ui'
import { updateAuthToken, getToken } from './loginTest'

// const BASE_URL = 'http://api.hucais.com:7979';
// const BASE_URL = 'http://hucais-gateway:7979';
// const BASE_URL = 'http://192.172.30.41:7979'; // 尹超
const BASE_URL = 'http://192.172.21.247:7979'

//页面接口通用接口 get
export function ajaxGet(url = '', data = {}, option = {}, isMock) {
  // 如果最后的参数为1，那么用测试版的数据
  if(arguments[arguments.length-1] != 1) {
    url = 'http://120.78.218.93:7300/mock/5b723f526f609f68edb5e5f9/api' + url
  };
  Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
  });
	return axios.get(url,data).then((result)=>{
        Indicator.close();
        // code==200的时候正常
        if(result.data.code == 200){
            return Promise.resolve(result.data);
        }else{
            MessageBox('提示', result.data.message);
            return Promise.reject(result.data)
        }
	}).catch((result)=>{
        Indicator.close();
        MessageBox('提示', '请求失败');
	})
}

//页面接口通用接口 Post
export function ajaxPost(url = '', data = {}, option = {}, isMock) {
  // 如果最后的参数为1，那么用测试版的数据
  console.info(arguments[arguments.length-1])
  if(arguments[arguments.length-1] != 1) {
    // url = 'http://192.172.30.41:7979' + url
    url = 'http://120.78.218.93:7300/mock/5b723f526f609f68edb5e5f9/api' + url
  };
  Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
  });
  if(axios.defaults.headers['Content-Type'] === 'application/x-www-form-urlencoded;charset=UTF-8') {
    data = qs.stringify(data, { indices: false });
  }
	return axios.post(url, data, option).then(result => {
		Indicator.close();
        // code==2的时候正常
        if(result.data.code == 200){
            return Promise.resolve(result.data);
        }else{
            MessageBox('提示', result.data.message);
            return Promise.reject(result)
        }
	}).catch(res => {
        Indicator.close();
        MessageBox('提示', '请求失败');
	})
}

// 首页-获取商品分类
export function getCategoryList(){
    return ajaxGet('/mdc/category/listAll')
}

// 首页-获取所有区域
export function queryAll(){
    return ajaxGet('/mdc/address/queryAll')
}

// 获取微信签名接口
export function createSignature(url){
    return ajaxPost('/mdc/store/createSignature',{
        url
    })
}

// 根据经纬度获取附近门店
export function getStoreBylatAndIng(longitude,latitude){
  axios.defaults.headers['Content-Type'] = 'application/json';
    return ajaxPost('/mdc/store/getStoreBylatAndIng',{
      longitude: longitude,
      latitude: latitude,
      pageNum: 1,
      pageSize: 10
    })
}

// 查询热门城市信息
export function getHotCity(){
    return ajaxGet('/mdc/address/getHotCity')
}

export function productList(){
    return ajaxGet('/mdc/product/list')
}

// 微商城-获取商品详情
export function getProductDetail(){
  return ajaxPost('/mdc/product/view')
}

// 第一次加入购物车
export function saveCart() {
  return ajaxPost('/mdc/cart/saveCart')
}

// 更新多条购物车信息
export function updateCartList() {
  return ajaxPost('/omc/cart/updateCartList')
}

// 获取购物车信息
export function getCartVo(data) {
  return ajaxPost('/omc/cart/getCartVo', data,1)
}

// 新增购物车信息
export function orderAddCart(data) {
  return ajaxPost('/omc/cart/addCart', data)
}

// 更新购物车数量
export function orderUpdateCart(data) {
  return ajaxPost('/omc/cart/updateProduct', data)
}

// 清空购物车
export function deleteCartProduct(data) {
  return ajaxPost('/omc/cart/deleteCartProduct', data)
}

// 全选多选
export function selectOrUnSelect(data) {
  return ajaxPost('/omc/cart/selectOrUnSelect', data)
}

// 获取用户积分及交易记录
export function getPointByUser(data) {
  console.log(data);
  return ajaxPost('/uac/point/getByUser', data)
}

// 确认下单信息
export function confirmationOrderDoc(data) {
    return ajaxPost(`/omc/order/confirmationOrderDoc/${data.storeId}`,)
}
// 微商城-获取用户收货地址列表
export function queryUserShippingListWithPage(data) {
    axios.defaults.headers['Content-Type'] = 'application/json';
    return ajaxPost(`/omc/shipping/queryUserShippingListWithPage`,data,1)
}
// 微商城-添加收货地址
export function addShipping(data) {
    axios.defaults.headers['Content-Type'] = 'multipart/form-data';
    return ajaxPost(`/omc/shipping/addShipping`,data,1)
}
// 微商城-创建订单
export function createOrderDoc(data) {
    axios.defaults.headers['Content-Type'] = 'application/json';
    return ajaxPost(`/omc/order/createOrderDoc`,data,1)
}
// 模拟登陆
// 获取验证码
ajaxPost(`${BASE_URL}/uac/auth/code/image`,1).then(result => {
    if(!!getToken()) return;
    // return;
    MessageBox({
        title: '提示',
        message: '<h1><img src="data:image/jpg;base64,' + result.result + '"></h1>',
        showCancelButton: true
    }).then((code) => {
        MessageBox.prompt('请输入验证码').then(({ value, action }) => {
          axios.defaults.headers['Content-Type'] = 'application/json';
          // axios.defaults.headers['Content-Type'] = ' ';
          let data = {
            username: 'yinchao',
            password: '1044510657yc',
            imageCode: value
          };
          data =  qs.stringify(data, { indices: false });
            ajaxPost(`${BASE_URL}/uac/auth/form?username=yinchao&password=1044510657yc&imageCode=${value}`, data, 1).then(result => {
                updateAuthToken(result.result.access_token)
            })
        });
    })
})

