window.globalConfig = {
	rootUrl: "",
	isDebug: true, //是否开启接口debug模式
	plat: 'NATIVE', //WEB、NATIVE
	timeout: 1000 * 180 //默认是3000毫秒
}
//***************************引入外部扩展方法***************************** */
import '@/assets/lib/extendFuc/extend.js'
//***************************引入框架************************************ */
import Vue from 'vue'
import App from './App'
import store from './vuex/store'
import router from './routers'

// 引入高德地图vue-amap
import VueAMap from 'vue-amap'

//引入手机ui框架 mint-ui
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

import '@/assets/css/main.scss'
import "@/assets/lib/swiper/swiper.animate1.0.2.min.js"
require('swiper-css/swiper.css')//引入swiper滑动组件
import '@/filters/globalFilter.js'
import '@/assets/lib/finger/alloy_finger.js'
import '@/assets/lib/finger/alloy_finger.vue.js'

import Navigation from 'vue-navigation'
import vueScrollBehavior from './components/common/scrolleBhavior/src/vue-scroll-behavior'
import VueLazyImage from "vue-lazy-images";
import VueAwesomeSwiper from 'vue-awesome-swiper'
/*********************************自定义组件 ****************** */
import utils from './utils'
import Page from './components/common/Page.vue'
import Tabs from './components/common/Tabs'
import Icon from './components/common/icon/index.vue';
import Datetime from './components/common/datetime/index.vue';
import Address from './components/common/address/index.vue';
//引入点击
import FastClick from 'fastclick'
if ('addEventListener' in document) {
	document.addEventListener('DOMContentLoaded', function () {
		FastClick.attach(document.body);
	}, false);
}

/*********************************给vue增加扩展方法start ****************** */
Vue.prototype.utils = utils;
/*********************************给vue增加扩展方法end ****************** */

Vue.use(VueAwesomeSwiper)
Vue.use(VueLazyImage)
Vue.use(MintUI)
Vue.use(VueAMap)

// 高德地图配置
VueAMap.initAMapApiLoader({
	key: '58d287727866bff32eb3c840d1cf18ed',
	plugin: ['AMap.Driving','AMap.Geocoder','AMap.Geolocation','AMap.CitySearch'],// 引入的地图插件
	v: '1.4.4'
});

Vue.component('page', Page)
Vue.component('tabs', Tabs)
Vue.component('icon', Icon)
Vue.component('datetime', Datetime)
Vue.component('hc-address', Address)
//设置打印模式
Vue.config.productionTip = false
//依赖组件
Vue.use(vueScrollBehavior, {
	router: router
})
Vue.use(Navigation, {
	router,
	store,
	moduleName: 'policy',
	keyName: 'RH',
	id: "scroll-content"
})
Vue.mixin({
	data() {
		return {
			screenWidth: document.documentElement.clientWidth || 0,
			screenHeight: document.documentElement.clientHeight,
			loaddingImg: "static/img/common/loading1.gif",
			isMobile: window.navigator.platform == 'iPhone'
		}
	},
	methods: {
		go(url) {
			this.$router.push(url)
		},
		back(url) {
			this.$router.back()
		},
		//返回首页
		toHome() {
			router.goBack("/baseHome")
		}
	}
})
 
new Vue({
	el: '#app',
	router,
	store,
	template: '<App/>',
	components: {
		App
	}
})




