// 门店
const store = r => require.ensure([], () => r(require('../mdc/store/store.vue')), 'store');
const confirmOrder = r => require.ensure([],() => r(require('../mdc/store/confirmOrder.vue')),'confirmOrder');
const goodsDetails = r => require.ensure([],() => r(require('../mdc/store/goodsDetails.vue')),'goodsDetails');
const storeList = r=> require.ensure([],() => r(require('../mdc/store/storeList.vue')),'storeList');
const addressIsNull = r=> require.ensure([],() => r(require('../mdc/store/addressIsNull.vue')),'addressIsNull');
const addAddress = r=> require.ensure([],() => r(require('../mdc/store/addAddress.vue')),'addAddress');
export default [{
	name: 'store',
	path: 'store',
	component: store,
	children: []
}, {
	name: 'confirmOrder',
	path: 'confirmOrder',
	component: confirmOrder,
	children: []
}, {
  name: 'goodsDetails',
	path: 'goodsDetails',
	component: goodsDetails,
	children: []
},{
	name: 'storeList',
	path: 'storeList',
	component: storeList,
	children: []
},{
	name: 'addressIsNull',
	path: 'addressIsNull',
	component: addressIsNull,
},{
	name: 'addAddress',
	path: 'addAddress',
	component: addAddress,
}]




