import axios from 'axios'
// 模拟登陆
import { getToken } from './loginTest'
let Authorization = !!getToken() ? 'Bearer ' + getToken() : 'Basic aHVjYWlzLWNsaWVudC11YWM6aHVjYWlzQ2xpZW50U2VjcmV0';
const $http = axios.create({
    // baseURL: 'http://120.78.218.93:7300/mock/5b723f526f609f68edb5e5f9/api', // 模拟数据的base_url
    //baseURL: 'http://192.172.30.7:7979/', // 测试数据的base_url 代虎
    // baseURL: 'http://192.172.30.201:7979/', // 测试数据的base_url 刘洪
    // baseURL: 'http://192.172.30.41:7979/', // 测试数据的base_url 伊超
    baseURL: 'http://192.172.21.247:7979/',
    timeout: 15000, // 请求超时时间,
    headers: {
      // 'deviceId': '1533724850516',
      'deviceId': '1535593658104',
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
      // 'Authorization': 'Basic aHVjYWlzLWNsaWVudC11YWM6aHVjYWlzQ2xpZW50U2VjcmV0',
      'Authorization': Authorization
    }
})

export default $http



